import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'忘记密码',
			phoneNum:'',
			resetFlag:0,
			nextFlag:0,
			errorMsg:''
		}
	},
	methods: {
		reset(){
			this.phoneNum='';
			this.resetFlag=0;
			this.nextFlag=0
		},
		changePhoneNum(e){
			var that = this;
			if(e.detail.value.length>0){
				that.phoneNum=e.detail.value;
				that.resetFlag=1;
			}else{
				that.phoneNum='';
				that.resetFlag=0;
			}
			if(e.detail.value.length>=10){
				that.nextFlag=1
			}else{
				that.nextFlag=0
			}
		},
		nextPage(){
			uni.navigateTo({
				url:'../setNewPassword/setNewPassword?phoneNum='+this.phoneNum,
			})
		}
	},
	components:{
		back
	}
}
